<template>
  <div>
    <div id="map"></div>
  </div>
</template>

<script>
import L from 'leaflet'

export default {
  name: 'HelloWorld',
  data () {
    return {
      map: null,
      tileX: 0,
      tileY: 0
    }
  },
  mounted () {
    this.initMap()
  },
  methods: {
    initMap() {
      const tileSize = 256;
      const numTilesX = 131;
      const numTilesY = 87;

      const bounds = L.latLngBounds(
        [-(numTilesY * tileSize), numTilesX * tileSize],
        [0, 0]
      )

      const map = L.map('map', {
        center: [numTilesY / 2, numTilesX / 2],
        zoom: 0,
        maxBounds: bounds,
        maxBoundsViscosity: 1.0,
        crs: L.CRS.Simple
      })

      const tileUrl = (coords) => {
        if (coords.x < 0 || coords.x > numTilesX) return ''
        if (coords.y < 0 || coords.y > numTilesY) return ''
        return `https://euroreborn.co.uk/LCD/leaflet-maps/0/${coords.z}/${coords.y}_${coords.x}.png`
      }

      L.TileLayer.Custom = L.TileLayer.extend({
        getTileUrl: function (coords) {
          return tileUrl(coords)
        }
      })

      const customTileLayer = new L.TileLayer.Custom('', {
        minZoom: -4,
        maxZoom: 0,
        tileSize: tileSize,
        continuousWorld: false,
        noWrap: true,
        attribution: 'Lilcooldoode Smells| <a href="http://leafletjs.com" title="A JS library for interactive maps">Leaflet</a>'
      })

      map.on('mousemove', this.updateCoordinates);
      customTileLayer.addTo(map);
      this.map = map;
      this.customizeAttribution();
    },
    updateCoordinates(event) {
      const lat = Math.abs(event.latlng.lat.toFixed(0));
      const long = Math.abs(event.latlng.lng.toFixed(0));
      this.tileX = Math.floor(long / 48);
      this.tileY = Math.floor(lat / 32);
      this.customizeAttribution();
    },
    customizeAttribution() {
      const attributionControl = this.map.attributionControl;
      attributionControl.setPrefix(`X: ${this.tileX}, Y: ${this.tileY}`);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#map {
    height: 99vh;
}

.leaflet-control-attribution {
    display: none !important;
}
</style>



